function sliderEvents() {
    var swiper = new Swiper('.events .swiper', {
        slidesPerView: '1',
        spaceBetween: 20,
        loop: false,
        navigation: {
            nextEl: '.events .swiper-button-next',
            prevEl: '.events .swiper-button-prev',
        },
        pagination: {
            el: ".events .swiper-pagination",
            clickable: true,
        },
    })
}

function sliderCoaches() {
    var swiper = new Swiper('.coaches .swiper', {
        slidesPerView: '2',
        spaceBetween: 25,
        navigation: {
            nextEl: '.coaches .swiper-button-next',
            prevEl: '.coaches .swiper-button-prev',
        },
        pagination: {
            el: ".coaches .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            741: {
                slidesPerView: 2,
            },
        }
    })
}

function sliderNews() {
    var swiper = new Swiper('.news .swiper', {
        slidesPerView: '3',
        spaceBetween: 25,
        navigation: {
            nextEl: '.news .swiper-button-next',
            prevEl: '.news .swiper-button-prev',
        },
        pagination: {
            el: ".news .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 15,
            },
            741: {
                slidesPerView: 3,
                spaceBetween: 15,
            },
            941: {
                slidesPerView: 3,
                spaceBetween: 25,
            }
        }
    })
}

$(document).ready(function() {

    sliderEvents();
    sliderCoaches();
    sliderNews();

    // Burger - Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-top').toggleClass('header-top--active')
        $('body').toggleClass('fixed-body')
    })

    // Up
    var btn = $('.up');
    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('up--active');
        } else {
            btn.removeClass('up--active');
        }
    });
    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });

})